<template>
  <v-container>
    <v-form ref="form" v-model="valid" lazy-validation>
      <v-row>
        <!--  -->
        <v-col cols="6">
          <v-text-field
            v-model="name"
            :counter="30"
            :rules="nameRules"
            label=""
            required
          ></v-text-field
        ></v-col>
        <!--  -->
        <v-col cols="6">
          <v-text-field
            v-model="lastname"
            :counter="30"
            :rules="lastnameRules"
            label=""
            required
          ></v-text-field
        ></v-col>
      </v-row>
      <v-row>
        <v-col cols="6"
          ><v-text-field
            v-model="email"
            :rules="emailRules"
            counter="50"
            label=""
            required
          ></v-text-field
        ></v-col>
        <v-col cols="6"
          ><v-text-field
            v-model="tel"
            :rules="telRules"
            :counter="10"
            label=""
            required
          ></v-text-field
        ></v-col>
      </v-row>

      <v-row>
        <v-col clos="2"
          ><v-text-field
            v-model="a_number"
            :rules="addressRules"
            :counter="20"
            label=""
            required
          ></v-text-field
        ></v-col>
        <v-col clos="2"
          ><v-text-field
            v-model="a_group"
            :rules="addressRules"
            :counter="20"
            label=""
            required
          ></v-text-field
        ></v-col>

        <v-col clos="4"
          ><v-text-field
            v-model="a_road"
            :rules="addressRules"
            :counter="20"
            label=""
            required
          ></v-text-field
        ></v-col>
        <v-col clos="4"
          ><v-text-field
            v-model="a_lane"
            :rules="addressRules"
            :counter="20"
            label=""
            required
          ></v-text-field
        ></v-col>
      </v-row>

      <v-row>
        <v-col clos="4"
          ><v-text-field
            v-model="a_subdis"
            :rules="addressRules"
            :counter="20"
            label=""
            required
          ></v-text-field
        ></v-col>
        <v-col clos="4"
          ><v-text-field
            v-model="a_dis"
            :rules="addressRules"
            :counter="20"
            label=""
            required
          ></v-text-field
        ></v-col>
        <v-col clos="4"
          ><v-text-field
            v-model="a_province"
            :rules="addressRules"
            :counter="20"
            label=""
            required
          ></v-text-field
        ></v-col>
      </v-row>

      <v-select
        v-model="select"
        :items="sex"
        :rules="[v => !!v || '']"
        label=""
        required
      ></v-select>

      <v-checkbox
        v-model="checkbox"
        :rules="checkboxRules"
        label=""
        required
      ></v-checkbox>

      <v-btn :disabled="!valid" color="success" class="mr-4" @click="validate">
        
      </v-btn>

      <v-btn color="error" class="mr-4" @click="reset">
        
      </v-btn>

      <v-row>
        <v-col cols="12">
          <!--  -->
          <v-card>
            <v-card-title>
              
              <v-spacer></v-spacer>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>
            <v-data-table
              :headers="headers"
              :items="desserts"
              :search="search"
            ></v-data-table>
          </v-card>
        </v-col>
      </v-row>

      <!-- <v-btn
      color="warning"
      @click="resetValidation"
    >
      
    </v-btn> -->
    </v-form>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    valid: false,

    name: "",
    nameRules: [
      v => !!v || "",
      v => (v && v.length <= 30) || " 30 "
    ],

    lastname: "",
    lastnameRules: [
      v => !!v || "",
      v => (v && v.length <= 30) || " 30 "
    ],

    email: "",
    emailRules: [
      v => !!v || " ",
      v => /.+@.+\..+/.test(v) || "",
      v => (v && v.length <= 50) || " 50 "
    ],

    tel: "",
    telRules: [
      v => !!v || "",
      v => (v && v.length <= 10) || " 10 "
    ],
    checkboxRules: [v => !!v || ""],
    select: null,
    items: ["Item 1", "Item 2", "Item 3", "Item 4"],
    sex: ["", ""],
    checkbox: false,
    a_number: "",
    a_group: "",
    a_road: "",
    a_lane: "",
    a_subdis: "",
    a_dis: "",
    a_province: "",
    addressRules: [
      v => !!v || "",
      v => (v && v.length <= 20) || " 20 "
    ],
    search: "",
    headers: [ {
        text: " - ",
        value: "fullname"},
      { text: "", value: "tel" },
      { text: "", value: "email" },
      { text: "", value: "sex" },
      { text: "", value: "address" }
    ],

    desserts: []
  }),

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        //   console.log(this.name);
        //   console.log(this.lastname);
        //   console.log(this.email);
        //   console.log(this.tel);
        //   console.log(this.select);
        //   console.log(this.a_number);
        //   console.log(this.a_group);
        //   console.log(this.a_lane);
        //   console.log(this.a_road);
        //   console.log(this.a_subdis);
        //   console.log(this.a_dis);
        //   console.log(this.a_province);

        this.desserts.push({
            fullname:`${this.name}${this.lastname}`,
            tel:`${this.tel}`,
            email:`${this.email}`,
            sex:`${this.select}`,
            address:`${this.a_number}.${this.a_group}.${this.a_road}.${this.a_lane}.${this.a_subdis}.${this.a_dis}.${this.a_province}`,
        });
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    }
  }
};
</script>

<style></style>
